import { Component, OnInit } from '@angular/core';

import {AppComponent} from '../../app.component';

import {UserService} from '../../services/user.service';

@Component({
  templateUrl: '../templates/groups-page.html'
})

export class GroupsPageComponent implements OnInit {

  public sortedGroups = null;
  public activeInfoCard: boolean;
  public groupInfo: any;
  public contactsArray: any;

  constructor(private appComponent: AppComponent,
              private userService: UserService) {}

  ngOnInit() {
    this.sortedGroups = null;
    this.groupInfo = null;
    this.activeInfoCard = false;
    this.contactsArray = this.userService.getUsers();
    this.createGroupsArray(this.contactsArray);
  }

  createGroupsArray(items) {
    items.forEach(item => {
      if (!this.sortedGroups) {
        this.sortedGroups = [];
        this.sortedGroups.push(this._createGroupObj(item));
      } else {
        let checkName = false;
        this.sortedGroups.forEach(group => {
          if (group.name === item.group) {
            group.contacts.push(item);
            checkName = true;
          }
        });
        if (!checkName) {
          this.sortedGroups.push(this._createGroupObj(item));
        }
      }
    });
  }

  _createGroupObj(data) {
    let group = {
      name: data.group,
      contacts: [data]
    };
    return group;
  }

  openGroup(group) {
    if (!this.groupInfo) {
      this.groupInfo = group;
    }
    if (this.activeInfoCard) {
      if (this.groupInfo.name === group.name) {
        this.activeInfoCard = false;
        this.groupInfo = null;
      } else {
        this.groupInfo = group;
      }
    } else {
      this.activeInfoCard = true;
    }
  }

  removeFromGroup(contact) {
    let noGroupContact = contact;
    noGroupContact.group = 'no group';
    this._updateContactsArray(noGroupContact);
    this.userService.update(this.contactsArray);
    this.sortedGroups = null;
    this.activeInfoCard = false;
    this.groupInfo = null;
    this.createGroupsArray(this.contactsArray);
  }

  _updateContactsArray(newData) {
    this.contactsArray.forEach(contact => {
      if (contact.id === newData.id) {
        contact = newData;
      }
    });
  }
}



