import {Component, OnInit, ViewChild, ElementRef, AfterViewInit} from '@angular/core';
import * as moment from 'moment';

import {AppComponent} from '../../app.component';

import {UserService} from '../../services/user.service';

@Component({
  templateUrl: '../templates/clock-page.html'
})

export class ClockPageComponent implements OnInit, AfterViewInit {

  @ViewChild('canvas') canvas: ElementRef;
  public ctx: CanvasRenderingContext2D;
  public radius: any;

  public currentDate: string;
  public currentTime: string;

  constructor(private appComponent: AppComponent,
              private userService: UserService) {
    setInterval(() => {
      this.currentTime = moment().format('h:mm:ss a');
    }, 1000);
  }

  ngOnInit() {
    this.currentDate = moment().format('Do MMM YYYY');
  }

  ngAfterViewInit() {
    let canvasEl = this.canvas.nativeElement;
    this.ctx = canvasEl.getContext("2d");
    this.radius = canvasEl.height / 2;
    this.ctx.translate(this.radius, this.radius);
    this.radius = this.radius * 0.9;
    setInterval(() => {this.drawClock()}, 1000);
  }

  drawClock() {
    this.drawFace(this.ctx, this.radius);
    this.drawNumbers(this.ctx, this.radius);
    this.drawTime(this.ctx, this.radius);
  }

  drawFace(ctx, radius) {
    let grad;
    ctx.beginPath();
    ctx.arc(0, 0, radius, 0, 2 * Math.PI);
    ctx.fillStyle = "#d499a1";
    ctx.fill();
    grad = ctx.createRadialGradient(0, 0, radius * 0.8, 0, 0, radius * 1.35);
    // grad.addColorStop(0, '#000');
    // grad.addColorStop(0.5, '#FFF');
    grad.addColorStop(1, '#FFF');
    ctx.strokeStyle = grad;
    ctx.lineWidth = radius * 0.2;
    ctx.stroke();
    ctx.beginPath();
    ctx.arc(0, 0, radius * 0.05, 0, 2 * Math.PI);
    ctx.fillStyle = "white";
    ctx.fill();
  }

  drawNumbers(ctx, radius) {
    let ang;
    let num;
    ctx.font = radius * 0.15 + "px arial";
    ctx.textBaseline = "middle";
    ctx.textAlign = "center";
    for (num = 1; num < 13; num++) {
      ang = num * Math.PI / 6;
      ctx.rotate(ang);
      ctx.translate(0, -radius * 0.80);
      ctx.rotate(-ang);
      ctx.fillText(num.toString(), 0, 0);
      ctx.rotate(ang);
      ctx.translate(0, radius * 0.80);
      ctx.rotate(-ang);
    }
  }

  drawTime(ctx, radius) {
    let now = new Date();
    let hour = now.getHours();
    let minute = now.getMinutes();
    let second = now.getSeconds();
    // hour
    hour = hour % 12;
    hour = (hour * Math.PI / 6) + (minute * Math.PI / (6 * 60)) + (second * Math.PI / (360 * 60));
    this.drawHand(ctx, hour, radius * 0.4, radius * 0.07);
    // minute
    minute = (minute * Math.PI / 30) + (second * Math.PI / (30 * 60));
    this.drawHand(ctx, minute, radius * 0.6, radius * 0.07);
    // second
    second = (second * Math.PI / 30);
    this.drawHand(ctx, second, radius * 0.75, radius * 0.02);
  }

  drawHand(ctx, pos, length, width) {
    ctx.beginPath();
    ctx.lineWidth = width;
    ctx.lineCap = "round";
    ctx.moveTo(0, 0);
    ctx.rotate(pos);
    ctx.lineTo(0, -length);
    ctx.stroke();
    ctx.rotate(-pos);
  }
}



