import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { ModalModule, AccordionModule } from 'ng2-bootstrap';

import { AppComponent } from './app.component';
import {ContactsPageComponent} from './pages/components/contacts.component';
import {GroupsPageComponent} from './pages/components/groups.component';
import {ClockPageComponent} from './pages/components/clock.component';

import {UserService} from './services/user.service';

const appRoutes: Routes = [
  { path: '', redirectTo: 'contacts', pathMatch: 'full' },
  { path: 'contacts', component: ContactsPageComponent },
  { path: 'groups', component: GroupsPageComponent },
  { path: 'clock', component: ClockPageComponent },
  { path: '**', redirectTo: 'contacts', pathMatch: 'full' }
];

@NgModule({
  declarations: [
    AppComponent,
    ContactsPageComponent,
    GroupsPageComponent,
    ClockPageComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    ModalModule.forRoot(),
    AccordionModule.forRoot()
  ],
  providers: [UserService],
  bootstrap: [AppComponent]
})
export class AppModule { }



